<template>
  <el-card>
    <!-- <el-row class="df aic">
      <el-col :offset="1" :span="2" class="df jcfe aic">当前店铺：</el-col>
      <el-col :span="6">
        <el-cascader class="w24" v-model="value" :options="options" @change="handleChange"></el-cascader>
      </el-col>
    </el-row>-->
    <el-row class="mb10 df aic">
      <el-col :span="2">订单号：</el-col>
      <el-col :span="10">
        <el-input placeholder="请输入订单号" v-model="order_number">
          <el-select class="store_name" v-model="select_store" slot="prepend" placeholder="请选择">
            <el-option
              v-for="item in store_name_list"
              :key="item.id"
              :value="item.id"
              :label="item.name"
            ></el-option>
          </el-select>
        </el-input>
      </el-col>
      <el-col :offset="2" :span="2">订单状态：</el-col>
      <el-col :span="6">
        <el-select class="w24" v-model="order_status" placeholder="请选择">
          <el-option
            v-for="item in order_status_list"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="2" class="df aic jcfe">
        <el-button type="primary" class="w22">检索</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-table :data="order_list" border>
          <el-table-column header-align="center" align="center" prop="order_number" label="销售订单号"></el-table-column>
          <el-table-column header-align="center" align="center" prop="order_money" label="订单金额"></el-table-column>
          <el-table-column header-align="center" align="center" prop="order_status" label="订单状态"></el-table-column>
          <el-table-column header-align="center" align="center"  label="商品列表">
            <template slot-scope="scope">
              <el-tag
                v-for="(item,index) in scope.row.commodity_list"
                :key="index"
                type="success"
                disable-transitions
                class="mb5"
              >{{item}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            header-align="center"
            align="center"
            prop="first_promotions"
            label="一级促销员"
          ></el-table-column>
          <el-table-column
            header-align="center"
            align="center"
            prop="second_promotions"
            label="二级促销员"
          ></el-table-column>
          <el-table-column header-align="center" align="center" prop="buyer" label="买家"></el-table-column>
          <el-table-column header-align="center" align="center" prop="order_time" label="订单时间"></el-table-column>
          <el-table-column header-align="center" align="center" label="操作">
            <template slot-scope="scope">
              <el-tooltip class="tooltip_item" effect="dark" content="查看详情" placement="top">
                <i class="el-icon-menu" @click="view_details(scope.row)"></i>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row class="mt10 df jcfe">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="current_page"
        :page-sizes="[50, 100, 200]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-row>

    <el-dialog title="订单详情" :visible.sync="dialog_visible" width="30%">
      <span>这是一段信息</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialog_visible = false">关闭</el-button>
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      order_status: "全部", //订单状态
      order_status_list: [
        { id: 1, name: "全部" },
        { id: 2, name: "待支付" },
        { id: 3, name: "已支付" },
        { id: 4, name: "送货中" },
        { id: 5, name: "已发送" },
        { id: 6, name: "已完成" }
      ],
      order_number: "", //订单号
      select_store: 1, //选中的店铺
      store_name_list: [
        { id: 1, name: "蓝拳妈妈" },
        { id: 2, name: "星巴克" },
        { id: 3, name: "瑞星" },
        { id: 4, name: "重庆分公司" }
      ],
      order_list: [
        {
          order_number: "1234567",
          order_money: "8888",
          order_status: "1",
          commodity_list: ["iphone6", "iphone7"],
          first_promotions: "代虎",
          second_promotions: "武文生",
          buyer: "胡高洁",
          order_time: "2019/11/11"
        }
      ],
      dialog_visible: false, //控制弹窗
      total: 0, //总共有多少条数据
      current_page: 1, //当前选择的第几页
      page_size: 50 //当前一页选择的条数
    };
  },
  methods: {
    handleSizeChange() {},
    handleCurrentChange() {},
    //查看详情
    view_details(res) {
      this.dialog_visible = true;
    }
  }
};
</script>

<style scoped>
.store_name {
  width: 120px;
}
.tooltip_item {
  cursor: pointer;
}
</style>


